import React, { useEffect, useMemo } from "react";
import { Badge, Card, Col, Dropdown, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { useState } from "react";
import { RiFilter2Fill } from "react-icons/ri";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";

import { getAdminDashboardAptList } from "../../../app/features/adminDashboard/adminDashboard.slice";
import DataTable from "../../../components/Table/DataTable";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import SearchInput from "../../../components/SearchInput";

export default function AdminAppointments() {
  const [selectedOptions, setSelectedOptions] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const pageSize = useMemo(() => 10, []);
  const [searchValue, handleSearch] = useDebouncedSearch(500);

  const dispatch = useDispatch();
  const { getAllApt, isLoading, isError, isSuccess } = useSelector(
    (state) => state?.adminDashboard
  );

  useEffect(() => {
    const finalData = {
      pageNo: pageNumber,
      size: pageSize,
      isPagination: true,
      search: searchValue ? searchValue : null,
      statusId:
        selectedOptions === "205"
          ? 205
          : selectedOptions === "204"
          ? 204
          : selectedOptions === "206"
          ? 206
          : selectedOptions === "203"
          ? 203
          : selectedOptions === "201"
          ? 201
          : selectedOptions === "202"
          ? 202
          : null,
    };
    dispatch(getAdminDashboardAptList(finalData));
  }, [dispatch, searchValue, selectedOptions, pageNumber]);

  const renderBadge = (status) => {
    return status === 204 ? (
      <Badge pill bg="info" className="upcoming-badge">
        Upcoming
      </Badge>
    ) : status === 201 ? (
      <Badge pill bg="primary" className="pending-badge">
        Pending
      </Badge>
    ) : status === 205 ? (
      <Badge pill bg="danger" className="missed-badge">
        Missed
      </Badge>
    ) : status === 206 ? (
      <Badge pill bg="light" className="completed-badge">
        Completed
      </Badge>
    ) : status === 203 ? (
      <Badge pill bg="warning" className="cancelled-badge">
        Cancelled
      </Badge>
    ) : (
      <Badge>Booked</Badge>
    );
  };

  const columns = [
    {
      dataField: "id",
      text: "ID",
    },
    {
      dataField: "doctorName",
      text: "Doctor Name",
      formatter: (cell) => {
        return cell || "N/A";
      },
    },
    {
      dataField: "patientName",
      text: "Patient Name",
      formatter: (cell) => {
        return cell || "N/A";
      },
    },
    {
      dataField: "appointmentDate",
      text: "Date",
      formatter: (cell) => {
        return cell ? moment(cell).format("DD/MM/YYYY") : "N/A";
      },
    },
    {
      dataField: "appointmentTime",
      text: "Time",
      formatter: (cell) => {
        return cell || "N/A";
      },
    },
    {
      dataField: "status",
      text: "Status",
      formatter: (text, row) => renderBadge(row?.statusId),
    },
  ];

  const options = [
    { label: "Missed", value: 205 },
    { label: "Upcoming", value: 204 },
    { label: "Completed", value: 206 },
    { label: "Cancelled", value: 203 },
    { label: "Pending", value: 201 },
    { label: "Booked", value: 202 },
  ];

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };

  return (
    <>
      <Card className="mb-0">
        <Card.Body className="p-0">
          <Row className="mx-2 my-2">
            <Col md={4} className="d-flex align-items-center">
              <Card.Title className="mb-0">Appointments</Card.Title>
            </Col>
            <Col
              md={8}
              className="d-flex align-self-center justify-content-end"
            >
              <SearchInput onChange={handleSearch} />

              <Dropdown className="apt-filter-dropdown">
                <Dropdown.Toggle
                  variant="secondary"
                  id="checkbox-dropdown"
                  className="custom-outline-filter py-2"
                >
                  <RiFilter2Fill size={28} className="color-99" />
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Form>
                    {options.map((option) => (
                      <Form.Check
                        key={option.value}
                        type="checkbox"
                        label={option.label}
                        value={option.value}
                        checked={
                          selectedOptions === null
                            ? false
                            : selectedOptions.includes(option.value)
                        }
                        onChange={(event) =>
                          setSelectedOptions(
                            selectedOptions === event.target.value
                              ? null
                              : event.target.value
                          )
                        }
                      />
                    ))}
                  </Form>
                </Dropdown.Menu>
              </Dropdown>
            </Col>
          </Row>
          {isLoading ? (
            <FullScreenLoader fullHeight={true} />
          ) : isSuccess ? (
            <span className="patient-datatable apt-badge">
              <DataTable
                tableColumns={columns}
                tableData={getAllApt?.data ? getAllApt?.data : []}
                keyField="id"
                pageNumber={pageNumber}
                totalRecords={getAllApt?.totalCounts}
                dataPerPage={pageSize}
                handlePageChange={handlePageChange}
              />
            </span>
          ) : isError ? (
            <p className="my-5 text-center text-danger fst-italic">
              Network Error
            </p>
          ) : null}
        </Card.Body>
      </Card>
    </>
  );
}
