import React, { useEffect } from "react";
import { Card, Row, Col } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";

import IMAGES from "../../../assets/images";
import Calendar from "./Calendar";
import { getAdminDashCount } from "../../../app/features/adminDashboard/adminDashboard.slice";
import AdminAppointments from "../../SuperAdmin/Dashboard/SuperAdminAppointments";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../utils/path";

export default function Dashboard() {
  const dispatch = useDispatch();
  const { adminDashCount, isLoading, isError, isSuccess } = useSelector(
    (state) => state?.adminDashboard
  );
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(getAdminDashCount());
  }, [dispatch]);
  return (
    <>
      <Row className="main-row patient-dashbaord">
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card
            className="h-100 admin__count-card"
            onClick={() => navigate(`${PATH.ADMIN_USERMANAGEMENT}?role=doctor`)}
          >
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Doctors</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {adminDashCount && adminDashCount?.totalDoctor}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.TOTAL_DOCTORS_ICON}
                alt="total doctors"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card
            className="h-100 admin__count-card"
            onClick={() =>
              navigate(`${PATH.ADMIN_USERMANAGEMENT}?role=patient`)
            }
          >
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Patients</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {adminDashCount && adminDashCount?.totalPatient}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.NEW_PATIENT_ICON}
                alt="total patients"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card
            className="h-100 admin__count-card"
            onClick={() => navigate(`${PATH.ADMIN_USERMANAGEMENT}?role=staff`)}
          >
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Staff</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {adminDashCount && adminDashCount?.totalStaff}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.TOTAL_STAFF_ICON}
                alt="total staff"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="my-3">
        <Col xl={8} xs={12}>
          <Row>
            <Col xs={12} className="mb-3">
              <AdminAppointments />
            </Col>
          </Row>
        </Col>
        <Col xl={4} xs={12}>
          <Row>
            <Col xs={12}>
              <Calendar />
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
}
